.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #E5E6E8;
  font-weight: 700;
  text-transform: uppercase;
  overscroll-behavior: auto;
  
  &__nav {
    margin-bottom: 48px;
  }

&__top {
  margin-bottom: 32px;

  @include on-tablet {
    margin-bottom: 56px;
  }
}

&__phone-number {
  display: block;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 22.4px;
  letter-spacing: 2px;
  color: inherit;
  text-decoration: none;
}


&__call-to-order {
  display: block;
  width: fit-content;
  padding-bottom: 5px;
  border-bottom: 1px solid $main-text-color;

  font-size: 16px;
  line-height: 22.4px;
  letter-spacing: 2px;
  color: inherit;
  text-decoration: none;
  }
}
