@mixin on-tablet  {
  @media (min-width: $tablet-min-width) {
   @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width){
    @content;
  }
}

@mixin content-padding-inline(){
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop{
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container{
  @include content-padding-inline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}

@mixin link-hover {
  &::after {
    content: '';
    position: absolute;
    bottom: -8px;
    display: block;
    height: 1px;
    width: 100%;
    background-color: $main-text-color;

    transition: transform $kebab-case;
    transform: scale(0);
    transform-origin: left;
  }
  
  &:hover::after {
    transform: scale(100%);
  }
}